.hero{
    background-color:radial-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.9))!important;
    background-size: cover!important;

}
.box{
    background: radial-gradient(rgba(250, 250, 250, 0.5), rgba(250, 250, 250, 0.9))!important;
    padding: 120px 50px;
    h1{
        font-weight: 900;
    }
}
.button{
    background: #c2702c;

    border: #c2702c solid 2px;
    transition: all ease-in-out 0.3s;
    &:hover{
        background: white;
        color: #c2702c;
        border: #c2702c solid 2px;

    }
}
.social{
    margin-top: 20px;
    width: 70px;
    height: 70px;
    border-radius: 4px;
    border: solid 3px #c2702c;
    position: relative;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    svg{
        position: absolute;
        max-width: 70px;
        fill: #c2702c;
        transition: all ease-in-out .3s;
        &:hover{
            background: #c2702c;
            fill: white;
            }
    }
}
#social{
    position: fixed;
    transform: translateY(-50%);
    top: 50%;
    height: 70px;
    width: 70px;
    left: 0;
    background: white;
    z-index: 09999;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 2px #c2702c;
    border-left: white;
    &:hover{
        background: #c2702c;
        svg{
            fill: white;
        }
    }
    svg{
        fill: #c2702c;
        height: 50px;
        width: 50px;
    }
}

.preloader{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: white;
    z-index: 10000000000000000;
}
.preloaderdone{
     animation-name: go;
     animation-duration: 1s;
     animation-delay: 0.4s;
     animation-fill-mode: forwards;
}


@keyframes go {
    from{
        transform: translateX(0);
        opacity: 1;
    }
    to{
        transform: translateX(100vw);
        opacity: 0;
    }
}
.scene{
    width: 100px;
    height: 100px;
    background: transparent;
    perspective: 1000px;
}

.flip{
    position: relative;
    width: 100%;
    height: 100%;
  //  transition:  transform 3s ease-in-out;
    transform-style: preserve-3d;
    animation-name: cardflip;
    animation-duration: 1s;
    animation-iteration-count: infinite;    
    &:hover{
        transform: rotateY(180deg);
    }
    .flip-card{
        display: flex;
        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
    }
    .flip-back{
        background: #ddd;
        transform: rotateY(180deg);
    }
    .flip-front{
        background: #DC901E;
    }
}

@keyframes cardflip{
    from{
        transform: rotateY(0deg);
    }
    to{
        transform: rotateY(360deg);
    }
}